li {
  text-decoration: none;
  list-style-type: none;
  font-size: 1.2rem;
}

.nav-block {
  height: 15vh;
}

  
  .menu-button {
    display: block;
    top: 7vh;
    right: 7vw;
    background-color: #fff;
    position: absolute;
    height: 6px;
    width: 20px;
    border-radius: 2px;

    &::before, &::after {
        display: block;
        right: 0;
        background-color: #fff;
        position: absolute;
        height: 6px;
        width: 30px;
        border-radius: 2px;
    }
  }
  
  .menu-button::before {
    content: '';
    margin-top: -10px;
  }
  
  .menu-button::after {
    content: '';
    margin-top: 10px;
  }
  