.about {
    background: linear-gradient(black 0%, rgba(27,114,99,0.708) 50%, black 100%);
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    height: 100vh;
    

    &__headshot-img {
        position: absolute;
        background-size: cover;
        background-position: center;
        opacity: 0.5;
        z-index: -1;
        overflow: hidden;
        object-fit: cover;
        height: 100%;
        width: clamp(800px, 100vw, 2000px);
    }

    &__greeting {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        font-size: clamp(4rem, 12vw, 10rem);
        cursor: pointer;
    }

    
}